* {
  padding: 0;
  margin: 0;
}
html {
  height: 100%;
}
body {
  background: yellow;
  position: relative;
  font-family: "Roboto", sans-serif;
  height: 100%;
}
.wrapper {
  display: flex;
  flex-direction: column;
  height: 100%;
}
.site-content {
  flex: 1 0 auto;
}
.container,
.menu-container {
  max-width: 1200px;
  margin: 0 auto 20px;
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.container {
  align-items: unset;
}
.content {
  max-width: 70vw;
  text-align: justify;
}
.content h1 {
  text-align: center;
}
.content h3 {
  margin: 10px 20px;
}
.sidebar {
  max-width: 20vw;
  padding: 10px 20px;
  margin-left: 2vw;
}
.sidebar h3 {
  margin: 5px auto;
}
.logo img {
  height: 100px;
}
.menu,
.authmenu {
  list-style-type: none;
  display: flex;
  flex-direction: row;
}
.menu li,
.authmenu li {
  font-family: "Lucida Sans", "Lucida Sans Regular", "Lucida Grande", "Lucida Sans Unicode", Geneva, Verdana, sans-serif;
}
.menu a,
.authmenu a {
  padding: 20px;
  display: block;
  text-decoration: none;
  color: black;
}
.authmenu li:first-child {
  margin-right: 1vw;
}
.menu li:hover {
  background: #e5e600;
}
.menu li:hover a {
  border-bottom: 1px solid black;
}
.Login {
  background: #97db0e;
  border-radius: 10px;
}
.Login:hover {
  background: #86c30c;
}
.Reg {
  background: #eb4e4e;
  border-radius: 10px;
}
.Reg:hover {
  background: #e83737;
}
footer {
  text-align: center;
  padding: 20px 0;
  background: #8c8c73;
  flex: 0 0 auto;
}
#modal-login,
#modal-reg {
  display: none;
  text-align: center;
  max-width: 150px;
  position: absolute;
  outline: 1px black solid;
  background: springgreen;
  padding: 20px;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}
#modal-login input,
#modal-reg input {
  margin: 5px 0;
  max-width: 140px;
}
#modal-login h3,
#modal-reg h3 {
  margin-bottom: 10px;
}
#modal-login button,
#modal-reg button {
  padding: 5px;
  border: solid 1px black;
  margin-top: 10px;
}
.send:hover {
  background: yellowgreen;
}
.cancel:hover {
  background: #eb4e4e;
}
@media screen and (max-width: 768px) {
  .container {
    flex-direction: column;
    margin: 5vw;
  }
  .content,
  .sidebar {
    max-width: 90vw;
  }
  #modal-login,
  #modal-reg {
    top: 50vh;
  }
  .menu-container {
    flex-direction: column;
    justify-content: center;
  }
  .menu-container li {
    margin-bottom: 10px;
  }
}
